import React from 'react';
import { NavLink } from 'react-router-dom';
import { CSSTransition } from 'react-transition-group';

import domtoimage from 'dom-to-image';

import './Head.css';


class Head extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      show: false,
      style: {
        fontSize: '1.2em'
      },
      activeTxt: '首页',
      BnnerTxtShow: false,
      bgSrc: ''
    }
  }

  componentDidMount(){
    this.buildBg();
  }

  toggerNavbar(){
    this.setState({
      show: !this.state.show
    })
  }

  getNavText(txt){
    this.buildBg();
    this.setState({
      BnnerTxtShow: false
    })
    setTimeout(() => {
      this.setState({
        activeTxt: txt,
        BnnerTxtShow: true
      })
    }, 250);
  }

  buildBg(){
    let box = document.createElement('div');
    box.className = 'box';

    document.getElementById('root').appendChild(box);

    for(let i = 0; i < 16; i++ ){
      let el = document.createElement('div');
      let rR = Math.floor(Math.random() * 256),rG = Math.floor(Math.random() * 256),rB = Math.floor(Math.random() * 256),rA = Math.random() * 0.1;
      // let sR = 0,sG = 0,sB = 0,sA = 0;
      el.className = 'item';
      el.style.width = '2.4rem';
      el.style.height = '2.4rem';
      el.style.background = `rgba(${rR},${rG},${rB},${rA})`;

      for(let j = 0; j < 6; j++){
        let rR = Math.floor(Math.random() * 256),rG = Math.floor(Math.random() * 256),rB = Math.floor(Math.random() * 256),rA = Math.random() * 0.8;
        let span = document.createElement('span');

        switch (j) {
          case 0: case 1:
            span.style.borderTopColor = `rgba(${rR},${rG},${rB},${rA})`;
            break;
          case 2: 
            // sR = rR,sG = rG,sB = rB,sA = rA;
            span.style.borderBottomColor = `rgba(${rR},${rG},${rB},${rA})`;
            break;
          case 3: 
            span.style.borderTopColor = `rgba(${rR},${rG},${rB},${rA})`;
            break;
          case 4: 
            span.style.borderLeftColor = `rgba(${rR},${rG},${rB},${rA})`;
            break;
          case 5: 
            span.style.borderRightColor = `rgba(${rR},${rG},${rB},${rA})`;
            break;
          default: 
            break;
        }
        el.appendChild(span)
      }
      document.querySelector('.box').appendChild(el)
    }
    let _that = this;
    let node = document.querySelector(".box");
			domtoimage.toPng(node)
	    .then(function (dataUrl) {
        _that.setState({
          bgSrc: dataUrl
        })
	    })
	    .catch(function (error) {
	      console.error('oops, something went wrong!', error);
      });

      setTimeout(() => {
        document.querySelector('#root').removeChild(document.querySelector('.box'))
      },1000)
  }

  render(){
    const isShow = this.state.show;

    // this.buildBg();

    const style = {
      backgroundImage: `url(${this.state.bgSrc})`,
      backgroundSize:  9.6 + Math.random() * 4 + 'rem'
    }

    // console.log(this.state.bgSrc)

    return (
      <div className="header" style={style} >
        <CSSTransition
          in={ this.state.BnnerTxtShow }
          classNames="progress"
          timeout={400}
          appear={true}
        >
          <span></span>
        </CSSTransition>
        <nav className="navbar">
          <div className="container">
            <NavLink to="/" className="router-link-exact-active router-link-active">
              <span className="navbar-site-name">技术张</span> 
              <span className="secondTitle" >专注于前端开发</span>
            </NavLink>
            <div className="navbar-toggler" onClick={ this.toggerNavbar.bind(this) }>
              <svg className="icon" style={ this.state.style }>
                <title>menu</title>
                <svg id="icon-menu" viewBox="0 0 1024 1024" width="100%" height="100%"><path d="M904 160H120c-4.4 0-8 3.6-8 8v64c0 4.4 3.6 8 8 8h784c4.4 0 8-3.6 8-8v-64c0-4.4-3.6-8-8-8zM904 784H120c-4.4 0-8 3.6-8 8v64c0 4.4 3.6 8 8 8h784c4.4 0 8-3.6 8-8v-64c0-4.4-3.6-8-8-8zM904 472H120c-4.4 0-8 3.6-8 8v64c0 4.4 3.6 8 8 8h784c4.4 0 8-3.6 8-8v-64c0-4.4-3.6-8-8-8z"></path></svg>
              </svg>
            </div>
            <div className={`navbar-links ${isShow ? 'show' : '' }`}> 
              <NavLink exact to="/" className="navbar-link" activeClassName="router-link-exact-active router-link-active" onClick={ this.getNavText.bind(this,'首页') }>首页</NavLink>
              <NavLink exact to="/posts" className="navbar-link" activeClassName="router-link-exact-active router-link-active" onClick={ this.getNavText.bind(this,'文章') }>文章</NavLink>
              <NavLink exact to="/posts/v/01.html" className="navbar-link" activeClassName="router-link-exact-active router-link-active" onClick={ this.getNavText.bind(this,(new Date()).getFullYear()+'年用VLOG记录我的生活点滴') }>生活</NavLink>
            </div>
          </div>
        </nav>
        <div className="banner">
          <div className="container">
            <div className="center">
              <CSSTransition
                in={ this.state.BnnerTxtShow }
                classNames="fade"
                timeout={400}
                appear={true}
              >
                <h1>{this.state.activeTxt}</h1>
              </CSSTransition>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Head;