import React from 'react';
import { Route, Redirect, Switch } from 'react-router-dom';
import Head from './components/Head/Head'
import Asides from './components/Aside/Aside'
import Footer from './components/Footer/Footer'
import Home from './pages/Home';
import Posts from './pages/Posts';

import './App.css';

function App() {
  const style = {
    color: '#1e90ff'
  }
  return (
    <div className="App">
      <header className="app-header">
        <Head />
      </header>
      <section className="show-aside container">
        <main className="main">
          <div className="target" >
            目标：<span style={style}> Vue, </span>  <span style={style}> React, </span> <span style={style}> 小程序, </span> <span style={style}> 公众号, </span>
            <span style={style}> Node </span>&nbsp;&nbsp;&nbsp;&nbsp;<a href="http://blog.zhangjunshan.com/" target="_blank" rel="noopener noreferrer" >[ 技术博客 ]</a>
          </div>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/posts" component={Posts}/>
            <Route exact path="/posts/:id" component={Posts} />
            <Redirect to="/" />
          </Switch>
        </main>
        <Asides />
      </section>
      <Footer />
    </div>
  );
}

export default App;
